@import url("https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,200;0,300;0,400;0,500;0,600;0,700;0,800;1,900&family=Rancho&display=swap");
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
//font-family: 'Poppins', sans-serif;

//font-family: 'Rancho', cursive;
@import "preload";

body {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: Poppins, sans-serif;
  min-height: 100vh;
}
a {
  text-decoration: none;
  cursor: pointer;
}
li {
  list-style: none;
}
//---------------- section
.header {
  width: 100%;
  min-height: 100vh;
  position: relative;
  background: #00122e;
}
.left-nav {
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  width: 120px;
  background: #182842;
  padding: 10px 0;
}
.logo {
  width: 56px;
  display: block;
  margin: auto;
  cursor: pointer;
}
.left-nav ul {
  margin-top: 160px;
}
.nav-img {
  width: 50px;
  display: block;
  margin: 10px auto;
  padding: 10px;
  cursor: pointer;
  opacity: 0.5;
  border-radius: 10px;
  transition: opacity 0.5s ease-in-out, background 0.5s ease-in-out;
}
.nav-img:hover {
  opacity: 1;
  background: #4d6181;
}
.nav-img.active {
  opacity: 1;
  background: #4d6181;
}
.container {
  margin-left: 120px;
  padding: 0 2.5%;
}
.top-icons {
  display: flex;
  align-items: center;
  justify-content: flex-end;
  padding: 25px 0;
}
.top-icons img {
  width: 25px;
  margin-left: 40px;
  cursor: pointer;
}
.row {
  margin-top: 15px;
  display: flex;
  justify-content: space-between;
}
.col-1 {
  flex-basis: 65%;
}
.col-2 {
  flex-basis: 33%;
}
.host-img {
  width: 100%;
  border-radius: 15px;
}
.controls {
  display: flex;
  justify-content: center;
  align-items: center;
}
.controls img {
  width: 40px;
  margin: 20px 10px;
  cursor: pointer;
  transition: transform 0.5s;
}
.controls .call-icon {
  width: 70px;
}
.controls img:hover {
  transform: translateY(-10px);
}
.joined {
  background: #182842;
  border-radius: 15px;
  padding: 30px 40px 30px;
  color: #fff;
  box-sizing: border-box;
}
.block-card {
  margin-top: 20px;
  display: grid;
  grid-template-columns: auto auto;
  grid-gap: 20px;
}
.joined img {
  width: 100%;

  border-radius: 10px;
  cursor: pointer;
}
.invite {
  background: #182842;
  border-radius: 15px;
  padding: 30px 20px 20px;
  color: #fff;
  margin-top: 20px;
  box-sizing: border-box;
  margin-bottom: 20px;
}
.invite-block {
  margin-top: 20px;
  display: grid;
  grid-template-rows: 1fr;
  grid-template-columns: 1fr 1fr 1fr 1fr 1fr;
  grid-gap: 20px;
}
.invite img {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  border-radius: 50%;
  cursor: pointer;
}
.invite img:last-child {
  display: none;
}
@media (max-width: 991px) {
  .invite-block {
    margin-top: 20px;
    display: grid;
    grid-template-rows: 1fr 1fr;
    grid-template-columns: 1fr 1fr 1fr;
    grid-gap: 20px;
  }
  .invite img:last-child {
    display: block;
  }
}
@media (max-width: 809px) {
  .row {
    flex-direction: column;
  }
  .block-card {
    grid-template-columns: auto auto auto;
    grid-gap: 10px;
  }
  .invite-block {
    margin-top: 20px;
    display: grid;
    grid-template-rows: 1fr;
    grid-template-columns: 1fr 1fr 1fr 1fr 1fr 1fr;
    grid-gap: 20px;
  }
}
@media (max-width: 767px) {
  .left-nav {
    width: 80px;
  }
  .container {
    margin-left: 80px;
  }
  .top-icons {
    padding: 15px 0;
  }
  .joined {
    padding: 30px 20px;
  }
}
@media (max-width: 444px) {
  .left-nav {
    width: 60px;
  }
  .container {
    margin-left: 60px;
  }
  .logo {
    width: 40px;
  }
  .left-nav ul {
    margin-top: 60px;
  }
  .nav-img {
    width: 40px;
    padding: 6px;
  }
  .controls img {
    width: 30px;
    margin: 20px 5px;
  }
  .controls .call-icon {
    width: 50px;
  }
  .invite img:last-child {
    display: none;
  }
  .invite-block {
    grid-template-rows: 1fr;
    grid-template-columns: 1fr 1fr 1fr 1fr 1fr;
    grid-gap: 15px;
  }
}
